import { AppBar, Box, Switch, Toolbar, Typography } from "@mui/material";
import React from "react";

const label = { inputProps: { "aria-label": "switch demo" } };

function Header({ darkMode, setDarkMode }) {
  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar
        position="static"
        sx={{
          height: "200px",
          justifyContent: "center",
        }}
      >
        <Toolbar sx={{ alignContent: "center" }}>
          {darkMode ? (
            <img
              style={{ borderRadius: "50%" }}
              src="https://e0.pxfuel.com/wallpapers/305/139/desktop-wallpaper-rick-and-morty-live-gif-inside-rick-and-morty-rick-and-morty-amoled.jpg"
              alt="headerimage"
              width="100%"
              height="auto"
            ></img>
          ) : (
            <img
              style={{ borderRadius: "50%" }}
              src="https://e0.pxfuel.com/wallpapers/163/136/desktop-wallpaper-minimal-rick-and-morty-tv-show-dark.jpg"
              alt="headerimage"
              width="100%"
              height="auto"
            ></img>
          )}
          <Typography
            variant="h6"
            component="div"
            sx={{ flexGrow: 1, fontSize: "3rem" }}
          >
            RICKY AND MORTY APP
          </Typography>
          <Switch
            {...label}
            onClick={() => {
              setDarkMode(!darkMode);
            }}
          />
          <Typography sx={{ fontFamily: "monospace" }}>Dark Mode</Typography>
        </Toolbar>
      </AppBar>
    </Box>
  );
}

export default Header;
