import React, { useCallback, useEffect, useState } from "react";
import "./Characters.css";
import { Avatar, Grid } from "@mui/material";
import MUIDataTable from "mui-datatables";

let page = 0;

function Characters() {
  const [characters, setCharacters] = useState([]);

  const [status, setStatus] = useState("");
  const [gender, setGender] = useState("");
  const [species, setSpecies] = useState("");

  const [pageCount, setPageCount] = useState(0);

  const getCharacters = useCallback(async () => {
    const fetchApiResponse = await fetch(
      `https://rickandmortyapi.com/api/character/?page=${page}&species=${species}&status=${status}&gender=${gender}`
    );
    const responseData = await fetchApiResponse.json();
    setCharacters([...responseData.results]);
    setPageCount(responseData.info.count);
  }, [species, gender, status]);

  const columns = [
    {
      name: "id",
      label: "ID",
      options: {
        filter: false,
        sort: true,
        display: false,
      },
    },
    {
      name: "name",
      label: "NAME",
      options: {
        filter: false,
        sort: true,
        setCellProps: () => ({ style: { justifyContent: "center" } }),
      },
    },
    {
      name: "image",
      label: "AVATAR",
      options: {
        filter: false,
        sort: false,
        setCellProps: () => ({ style: { justifyContent: "center" } }),
        customBodyRender: (value) => {
          return (
            <Avatar
              sx={{
                height: "130px",
                width: "130px",
              }}
              src={value}
            ></Avatar>
          );
        },
      },
    },
    {
      name: "species",
      label: "SPECIES",

      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "gender",
      label: "GENDER",

      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "status",
      label: "Status",
      options: {
        filter: true,
        sort: false,
      },
    },
  ];

  const options = {
    filterType: "dropdown",
    count: pageCount,
    onTableChange: async (action, tableState) => {
      if (action === "changePage") {
        page = tableState.page + 1;
        getCharacters(page);
      }
    },
    rowsPerPage: 20,
    rowsPerPageOptions: [20],
    serverSide: true,
    onFilterChange: (changedColumn, filterList) => {
      setSpecies(filterList[3]);
      setGender(filterList[4]);
      setStatus(filterList[5]);
    },

    search: false,
  };

  useEffect(() => {
    getCharacters();
  }, [getCharacters]);

  return (
    <Grid>
      <MUIDataTable data={characters} columns={columns} options={options} />
    </Grid>
  );
}

export default Characters;
